<template>
  <div class="row">
    <CoolLightBox
      :items="images"
      :useZoomBar="true"
      :index="index"
      @close="index = null"
    >
    </CoolLightBox>
    <div class="flex-x-center" style="padding: 0px 10px; margin-top: 30px">
      <!-- <h4 class="mb-0">Yiu's Villa Supplier</h4> -->
      <img src="/logo.png" width="150px" alt="" />
      <p class="mb-0">
        {{ order.date }}
      </p>
    </div>
    <div class="content" style="margin-bottom: 30px">
      <div class="card-product">
        <h5 style="margin: 15px 0px">Customer</h5>
        <p class="gray-bg">Order ID : {{ order.id }}</p>

        <p class="gray-bg">Name : {{ order.customer_name }}</p>
        <p class="gray-bg">
          Phone :
          {{ order.phone }}
        </p>
        <p class="gray-bg">
          Address :{{ order.address.address }},
          {{ order.address.city.split("_").join(" ") }},
          {{ order.address.state.split("_").join(" ") }}
        </p>
      </div>

      <div class="card-product">
        <h5 style="margin: 15px 0px">Items</h5>
        <table id="products">
          <tr>
            <th>No.</th>
            <th>Item</th>
            <th>Rate</th>
            <th>Subtotal</th>
          </tr>
          <tr v-for="(product, index) in order.products" :key="index">
            <td>
              {{ index + 1 }}
            </td>
            <td class="item-row">
              <img
                :src="product.image"
                class="item-img"
                @click="viewImage(product.image)"
                alt="Image"
                @load="loaded"
              />
              <p>{{ product.name || "-" }}</p>
            </td>

            <td>{{ product.quantity }}x{{ product.sale_price }}</td>
            <td>
              {{ parseFloat(product.subtotal).toFixed(2) }}
            </td>
          </tr>
        </table>
      </div>
      <div class="card-product">
        <h5 style="margin: 15px 0px">
          Total price of {{ order.products.length }} Items
        </h5>
        <p class="gray-bg">Subtotal : {{ order.subtotal }}</p>
        <p class="gray-bg">
          Percentage({{ order.supplier_percentage }}%) :
          {{
            (
              parseFloat(order.subtotal) *
              (parseFloat(order.supplier_percentage) / 100)
            ).toFixed(2)
          }}
        </p>
        <p class="gray-bg">Total : {{ order.total }}</p>
        <p class="gray-bg">
          Total (MMK) :{{ order.total + " x " + order.money_price + "MMk = " }}
          {{ parseInt(order.total) * parseInt(order.money_price) }} MMK
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      order: {
        products: [],
      },
      loading_: null,
      images: [],
      index: null,
      openImage: false,
      gettingSale: true,
    };
  },
  created() {
    const queries = new URLSearchParams(window.location.search);
    const sid = queries.get("sid");
    const oid = queries.get("oid");
    const date = queries.get("date");

    this.getOrder(sid, oid, date);
  },
  methods: {
    loaded(e) {
      e.path[0].style.height = e.path[0].clientWidt + "px";
    },
    viewImage(img) {
      this.images = [img];
      this.index = 0;
    },
    getOrder(sid, oid, date) {
      var link = `https://osassist.pythonanywhere.com/api/sale-view/?sid=${sid}&oid=${oid}&date=${date}`;
      axios
        .get(link)
        .then((response) => {
          this.order = response.data;
          response.data.products.forEach((item) => {
            var img = new Image();
            img.src = item.image;
          });
          const loader = document.querySelector(".page-loader");
          loader.className += " hidden";
          setTimeout(() => {
            loader.className += " display_none";
          }, 1000);
        })
        .catch(() => {
          alert("Error!");
        });
    },
  },
};
</script>

    <style >
body {
  margin: 0px !important;
}
.main {
  position: relative;
  max-width: 100vw;
  overflow-x: hidden;
}
#products {
  font-family: Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  width: 100%;
}

#products td,
#products th {
  border: 1px solid rgb(235, 235, 235);
  padding: 8px;
}
th {
  font-size: 12px;
}
#products tr:hover {
  background-color: #f2f2f2;
}

#products th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: #6951a4;
  color: white;
}

.item-row {
  display: flex;
  align-items: center;
}
.item-row img {
  width: 60px;
  height: 60px;
  object-fit: cover;
  margin-right: 15px;
}
.px-2 {
  margin-right: 10px;
  margin-left: 10px;
}
.pt-3 {
  padding-top: 15px;
}
.mb-3 {
  margin-bottom: 15px;
}
.route-view {
  /* height: 100vh; */
}
.cool-lightbox .cool-lightbox__inner {
  padding: 0px !important;
}
.row {
  padding: 0px 10px;
}
.cool-lightbox {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  width: 100vw !important;
  height: 100vh !important;
  padding: 0px !important;
}
button.cool-lightbox-toolbar__btn {
  background: rgb(0, 0, 0, 0.3) !important;
  color: #fff !important;
  border-radius: 5px !important;
}
.cool-lightbox .cool-lightbox__slide img {
  width: 100%;
  height: 100%;
  border-radius: 0px !important;
  object-fit: contain;
}
.cool-lightbox-toolbar {
  top: 5px !important;
  right: 5px !important;
}
.flex-x-center {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
p,
a {
  font-size: 16px !important;
}
.item-img {
  width: 100%;
  border-radius: 5px;
  object-fit: cover;
  filter: brightness(0.9);
  cursor: pointer;
}
.go-to-app {
  padding: 8px 15px;
  display: block;
  background: #6951a4;
  color: #fff;
  text-decoration: none;
  margin-top: 10px;
  border-radius: 5px;
}

.card-product {
  background: rgb(255, 255, 255);
  box-shadow: 0px 4px 25px -8px rgb(0 0 0 / 20%);
  border-radius: 10px;
  padding: 8px !important;
  margin: 15px 0px !important;
}
.gray-bg {
  background: rgb(0, 0, 0, 0.05);
  padding: 8px;
  border-radius: 5px;
  text-align: start !important;
  margin: 5px 0px !important;
}
</style>